@import url(https://fonts.googleapis.com/css?family=Caveat);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}




input[type="number"] { -moz-appearance: textfield; }

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}
.Skill button {
  padding: 0;
  box-shadow: 0;
  /* border: 1px black solid; */
  margin-left: 0.2em;
  height: 0.9em;
  width: 0.9em;
}

.buttons {
  display: flex;
  float: right;
}

.Skill .buttons button  {
  border: 0px;
}

.Skill {
  
}
.Attribute button {
  padding: 0.2em;
  box-shadow: 0;
  border: 1px black solid;
  margin-left: 0.2em;
}

.Attribute button:hover {
  box-shadow: 0 0 1px;
}

.right-float {
  float: right;
}

.Attribute {
  width: 40%;
  position: relative;
  float: left;
}


.Attribute select:active,
.Attribute select:hover {
  outline-color: black;
}
.Character {
  background: white;
  border: 1px solid lightgrey;
  padding: 1em;
  margin: 1em;
  position: relative;
}

.Character .right-top {
  position: absolute;
  padding: 1em;
  top: 0;
  right: 0;
}

.Character .max-width {
  max-width: 600px;
}
.character-stuff {
  /* display: flex; */
}

.Character .character-stuff div {
  border-bottom: 1px black solid;
  padding: 1em 1em 0 1em;
  /* display: flex; */
}

.Character input {
  /* display: inline; */
  background: none;
  width: 100%;
  border: 0px;
}

.clearit {
  clear: both;
}

